import React from 'react';
import './MarketAnalysisPanel.css';

const MarketAnalysisPanel = ({ aiSignal, isLoading, isRequestingSignal, onRequestNewSignal }) => {
    if (isLoading) {
        return (
            <div className="market-analysis-panel loading-state">
                <p>Loading Market Analysis...</p>
            </div>
        );
    }

    // Check if the necessary data exists in the signal
    const hasAnalysisData = aiSignal &&
                            aiSignal.trend &&
                            aiSignal.volume &&
                            aiSignal.buyer_percentage != null;

    // Determine class for the trend
    const getTrendClass = (trend) => {
        if (!trend) return '';
        const lowerTrend = trend.toLowerCase();
        if (lowerTrend.includes('up')) return 'trend-up';
        if (lowerTrend.includes('down')) return 'trend-down';
        return 'trend-sideways';
    };

    return (
        <div className="market-analysis-panel">
            {/* Conditionally render the analysis section */}
            {hasAnalysisData && (
                <div className="analysis-content">
                    <h4 className="panel-title">Market Analysis</h4>
                    <div className="analysis-grid">
                        {/* Trend Card */}
                        <div className={`analysis-card ${getTrendClass(aiSignal.trend)}`}>
                            <div className="card-label">Trend</div>
                            <div className="card-value">{aiSignal.trend}</div>
                        </div>

                        {/* Volume Card */}
                        <div className="analysis-card">
                            <div className="card-label">Volume</div>
                            <div className="card-value">{aiSignal.volume}</div>
                        </div>
                    </div>
                </div>
            )}

            {/* Buy/Sell Pressure Bar Section */}
            {hasAnalysisData && (
                <div className="pressure-bar-section">
                    <div className="pressure-bar-label">
                        <span>Buy Pressure ({aiSignal.buyer_percentage.toFixed(0)}%)</span>
                        <span>Sell Pressure ({(100 - aiSignal.buyer_percentage).toFixed(0)}%)</span>
                    </div>
                    <div className="pressure-bar">
                        <div className="buy-pressure" style={{ width: `${aiSignal.buyer_percentage}%` }}></div>
                    </div>
                </div>
            )}

            {/* Always render the manual request section */}
            <div className={`signal-request-section ${!hasAnalysisData ? 'no-border' : ''}`}>
                <p>
                    {hasAnalysisData 
                        ? '?  AI' 
                        : ',  AI'}
                </p>
                <div className="signal-request-buttons">
                    <button
                        className="request-button buy"
                        onClick={() => onRequestNewSignal('BUY')}
                        disabled={isLoading || isRequestingSignal}
                    >
                        {isRequestingSignal ? '...' : ' BUY'}
                    </button>
                    <button
                        className="request-button sell"
                        onClick={() => onRequestNewSignal('SELL')}
                        disabled={isLoading || isRequestingSignal}
                    >
                        {isRequestingSignal ? '...' : ' SELL'}
                    </button>
                </div>
            </div>
        </div>
    );
};

export default MarketAnalysisPanel;
