// src/components/ProfileModal.js
import React, { useState } from 'react';
import { useAuth } from '../context/AuthContext';
import './ProfileModal.css';

const ProfileModal = ({ isOpen, onClose }) => {
    const { user, token, changePassword, updateUserProfile } = useAuth(); //  token  user  AuthContext
    const [currentPassword, setCurrentPassword] = useState('');
    const [newPassword, setNewPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [selectedFile, setSelectedFile] = useState(null);
    const [preview, setPreview] = useState(null);

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError('');
        setSuccess('');

        if (newPassword !== confirmPassword) {
            setError("");
            return;
        }

        // 
        const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d).{8,}$/;
        if (!passwordRegex.test(newPassword)) {
            setError(" 8 , , , ");
            return;
        }

        setIsLoading(true);
        const result = await changePassword(currentPassword, newPassword);
        setIsLoading(false);

        if (result.success) {
            setSuccess(result.message || '!');
            setTimeout(() => {
                handleClose();
            }, 2000);
        } else {
            setError(result.message || '');
        }
    };

    const handleFileChange = (e) => {
        if (e.target.files && e.target.files[0]) {
            const file = e.target.files[0];
            setSelectedFile(file);
            setPreview(URL.createObjectURL(file));
        }
    };

    const handleImageUpload = async () => {
        if (!selectedFile) {
            setError("");
            return;
        }
        setIsLoading(true);
        setError('');
        setSuccess('');

        const formData = new FormData();
        formData.append('profileImage', selectedFile);

        try {
            const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:3001';
            const response = await fetch(`${apiUrl}/api/auth/upload-profile-image`, {
                method: 'POST',
                headers: {
                    //  token  request  ()
                    'Authorization': `Bearer ${token}`,
                },
                body: formData,
                //  'Content-Type' header  FormData
                // browser 
            });
            const result = await response.json();

            if (!response.ok) {
                throw new Error(result.message || '');
            }

            setSuccess(result.message);
            // Use the function from AuthContext to update the user state locally
            updateUserProfile({ profileImageUrl: result.imageUrl });

        } catch (err) {
            setError(err.message || '');
        } finally {
            setIsLoading(false);
        }
    };

    const handleClose = () => {
        // 
        setCurrentPassword('');
        setNewPassword('');
        setConfirmPassword('');
        setError('');
        setSuccess('');
        setSelectedFile(null);
        setPreview(null);
        onClose();
    };

    if (!isOpen) {
        return null;
    }

    // --- Logic to safely create the profile image URL ---
    const defaultAvatar = 'https://via.placeholder.com/150';
    let profileImageSrc = defaultAvatar;

    if (preview) {
        // 1.   preview 
        profileImageSrc = preview;
    } else if (user && user.profileImageUrl) {
        // 2.  URL  user object
        const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:3001';
        //  URL  API URL  path 
        profileImageSrc = `${apiUrl}${user.profileImageUrl}`;
    }
    // 3.   defaultAvatar 

    return (
        <div className="modal-overlay" onClick={handleClose}>
            <div className="modal-content profile-modal" onClick={e => e.stopPropagation()}>
                <button className="close-button" onClick={handleClose}>&times;</button>
                <h2></h2>
                
                <div className="profile-section profile-picture-section">
                    <img 
                        src={profileImageSrc}
                        alt="Profile" 
                        className="profile-picture"
                    />
                    <input 
                        type="file" 
                        id="profile-upload" 
                        style={{ display: 'none' }} 
                        onChange={handleFileChange}
                        accept="image/png, image/jpeg"
                    />
                    <label htmlFor="profile-upload" className="upload-button"></label>
                    {selectedFile && (
                        <button onClick={handleImageUpload} className="submit-button" disabled={isLoading}>
                            {isLoading ? '...' : ''}
                        </button>
                    )}
                </div>

                <h3 className="section-title"></h3>
                <form onSubmit={handleSubmit}>
                    <div className="form-group">
                        <label htmlFor="current-password"></label>
                        <input
                            type="password"
                            id="current-password"
                            value={currentPassword}
                            onChange={(e) => setCurrentPassword(e.target.value)}
                            required
                            disabled={isLoading}
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="new-password"></label>
                        <input
                            type="password"
                            id="new-password"
                            value={newPassword}
                            onChange={(e) => setNewPassword(e.target.value)}
                            required
                            disabled={isLoading}
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="confirm-password"></label>
                        <input
                            type="password"
                            id="confirm-password"
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                            required
                            disabled={isLoading}
                        />
                    </div>
                    {error && <p className="error-message">{error}</p>}
                    {success && <p className="success-message">{success}</p>}
                    <button type="submit" className="submit-button" disabled={isLoading || !currentPassword || !newPassword}>
                        {isLoading ? '...' : ''}
                    </button>
                </form>
            </div>
        </div>
    );
};

export default ProfileModal;