// src/components/StatisticsModal.js
import React from 'react';
import WinLossPieChart from './WinLossPieChart';
import './StatisticsModal.css';
import './PredictionStats.css'; // Assuming styles for stats grid are here

/**
 * StatsGrid Component
 * This component is now responsible ONLY for displaying the grid of statistics.
 * It no longer renders its own title.
 */
const StatsGrid = ({ statsData }) => {
    // If there's no data or no trades, display a message.
    if (!statsData || !statsData.totalTrades) {
        return (
            <div className="stats-message">
                No statistics available yet.
            </div>
        );
    }

    //  wins, losses, winRate  API 
    const { totalTrades, wins, losses, winRate } = statsData;

    // 
    const safeWinRate = typeof winRate === 'number' ? winRate : 0;

    return (
        <div className="stats-grid">
            <div className="stat-item">
                <span className="stat-label">Total Signals</span>
                <span className="stat-value">{totalTrades}</span>
            </div>
            <div className="stat-item win">
                <span className="stat-label">Wins</span>
                <span className="stat-value">{wins || 0}</span>
            </div>
            <div className="stat-item loss">
                <span className="stat-label">Losses</span>
                <span className="stat-value">{losses || 0}</span>
            </div>
            <div className="stat-item win-rate">
                <span className="stat-label">Win Rate</span>
                <span className="stat-value">{safeWinRate.toFixed(1)}%</span>
            </div>
        </div>
    );
};

/**
 * StatisticsModal Component
 * The main modal container, responsible for the title and overall layout.
 */
const StatisticsModal = ({ isOpen, onClose, aiStats }) => {
    if (!isOpen) {
        return null;
    }

    // Prepare data for the pie chart
    const wins = aiStats?.wins || 0;
    const losses = aiStats?.losses || 0;

    return (
        <div className="modal-overlay" onClick={onClose}>
            <div className="modal-content stats-modal" onClick={e => e.stopPropagation()}>
                <button className="close-button" onClick={onClose}>&times;</button>
                
                <h3 className="stats-title">AI Model Performance</h3>
                
                <div className="stats-content-wrapper">
                    <StatsGrid statsData={aiStats} />
                    <WinLossPieChart wins={wins} losses={losses} />
                </div>
            </div>
        </div>
    );
};

export default StatisticsModal;
